/*-- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -*\
	#FORM
\*-- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -- -*/
form {
	button {
		background-color: $color-accent-blue;
		border: 3px solid #fff;
		box-shadow: 0 0 1px #000;
		color: #fff;
		cursor: pointer;
		font: inherit;
		font-weight: bold;
		padding: .5em 1em;
		transition: box-shadow .2s ease-out, background-color .2s ease-out;

		&:hover {
			background-color: lighten($color-accent-blue, 10%);
			box-shadow: 0 0 3px #000;
		}

		&:focus {
			box-shadow: 0 0 5px #ff9200;
			outline: none;
		}
	}

	fieldset {
		border: none;
		padding: 0;
	}

	textarea {
		border: 3px solid $color-border;
		display: block;
		font: 14px/1.2 Monaco, Consolas, "Lucida Console", monospace;
		resize: vertical;
		padding: .5em;
		width: 100%;
	}

	input {
		display: block;

		&[type="file"] {
			height: 0;
			width: 0;

			&:focus {
				outline: none;
			}
		}
	}

	label {
		display: block;
	}

	fieldset + button,
	label + label,
	label + button,
	label + fieldset {
		margin-top: 1em;
	}
}
